import DataService  from '../../service/DataService'


let buttons =
[
    {
        title: 'Request New Product Type',
        iconClass: 'fa fa-plus',
        callbackfunction: 'RequestNewProductType',
        href: '',
        additionalClass: '',
        isdisabled: false,
        isvisible: true,
        checkPrivilege: false
    },
    {
        title: 'Delete',
        iconClass: 'fa fa-trash',
        callbackfunction: 'deleteItem',
        additionalClass: '',
        isdisabled: true,
        isvisible: true,
        checkPrivilege: false
    },
    
];


let leftsearchSchema = [
    {
    fieldName: "Product Name",
    fieldType: "text",
    fieldIcon: "fa-file",
    paramName:"search",
    fieldSearchConditionName: [''],
    isSearch: false,
    value: "",
    listOptions: []
}, 
{
    fieldName: 'Product Type',
    fieldType: "ddl-check",
    fieldIcon: "fa-user",
    fieldSearchConditionName: [''],
    isSearch: false,
    value: "",
    listOptions: []
}

]


 
function bindleftCommonSearchdropdown(instance,modulename, filtername) {
 
    var vm = instance;
    if (filtername == "Product_Type") {
        vm.isLoading = true;
        var url = `type=ITEMCODE&issueTo=&locationId=`;
         DataService.GetAssetTypeCustom(url).then(response => {
            vm.AssetType = JSON.parse(response.data);
            if (vm.AssetType != null) {
                vm.AssetType.forEach((item) => {
                  let obj = {
                    name: item.ASSETTYPE,
                    value: item.ASSETID,
                  };
                  vm.leftSearchFields[1].listOptions.push(obj);
                });
                return;
              }
        })
    }



}
export default { buttons,leftsearchSchema,bindleftCommonSearchdropdown};